




















import { Component, Vue } from 'vue-property-decorator';
import { Getter, State } from 'vuex-class';
import { IArticle, ICommercialItem } from '@/types/interface';
import Banner from '../../components/Banner.vue';
import AdvertisingBannerList from '../../components/AdvertisingBannerList.vue';

@Component({
  components: {
    Banner,
    AdvertisingBannerList,
  },
})

export default class ArticleContent extends Vue {
  @State('commercialList') stateCommercialList!: ICommercialItem[];
  @Getter('articleById', { namespace: 'article' }) articleById!: (id: number) => IArticle;

  link() {
    this.$router.go(-1);
  }

  content: IArticle = {
    id: 0,
    title: '',
    url: '',
    imgUrl: '',
    subTitle: '',
    source: '',
    brief: '',
    hashtags: [],
    note: '',
  }

  created() {
    const {
      id,
    } = this.$route.query;

    if (id) {
      this.content = this.articleById(parseInt(id.toString(), 10));
    }
  }

  showShare() {
    return navigator.share;
  }

  share() {
    const shareData = {
      url: this.content.url,
      title: this.content.title,
      text: this.content.brief,
    };
    if (navigator.share) {
      navigator.share(shareData);
    }
  }
}
